import $ from "jquery";
export default function(){
    var script = document.createElement('script');  
        script.src = "https://cdn.bootcss.com/jquery/3.3.1/jquery.min.js";  
        document.body.appendChild(script);  
        //
    var $hammer = $("#hammer"),
        $tips = $(".info"),
        $eggList = $(".egg li"),//
        $egg = $(".goldegg"),//
        $change = $("#change"),//
        length = $egg.length,
        data = {count: 5},//
        arr = [],
        openArr,//
        rem = 75;
        
        $egg.eq(length).addClass("jump");
        $tips.eq(length).show();
        setInterval(function () {
            //
            length++;
            length %= 9;
            $egg.eq(length - 1 < 0 && 8 || length - 1).removeClass("jump");
            $tips.eq(length - 1 < 0 && 8 || length - 1).hide();
            reback();
            $egg.eq(length).addClass("jump");
            $tips.eq(length).show();
        }, 1000);

        function reback() {
            if (!$egg.eq(length).hasClass("init")) {//
                length++;
                length %= 9;
                reback();
            }
        }
    
        /**/
        for (var i = 0; i < length; i++) {
            $egg.eq(i).data("mark", true);//true
            $eggList.eq(i).data("i", i);
            $eggList.eq(i).click(function () {
                //
                if (data.count > 0) {
                    $egg.eq($(this).data("i")).data("mark") ? eggChange($(this).data("i")) : alert("");//
                } else {
                    alert("0");
                }
            });
        }
    
        /**/
        function eggChange(i) {
            //
            data.count--;
            $change.html(data.count);
            $hammer.removeClass("shak");//
            //
            (i === 0 || i === 3 || i === 6) && ($hammer.css("left", 165 / rem + "rem"));
            (i === 1 || i === 4 || i === 7) && ($hammer.css("left", 415 / rem + "rem"));
            (i === 2 || i === 5 || i === 8) && ($hammer.css("left", 665 / rem + "rem"));
            (i === 0 || i === 1 || i === 2) && ($hammer.css("top", 60 / rem + "rem"));
            (i === 3 | i === 4 || i === 5) && ($hammer.css("top", 280 / rem + "rem"));
            (i === 6 | i === 7 || i === 8) && ($hammer.css("top", 500 / rem + "rem"));
            //
            setTimeout(function () {
                $hammer.css({
                    left: 665 / rem + "rem",
                    top: 60 / rem + "rem"
                });
            }, 1500);
    
            //
            setTimeout(function () {
                $hammer.addClass("hit");
                setTimeout(function () {
                    $egg.eq(i).prop("src", "data:image/png;base64,");
                }, 300);
                setTimeout(function () {
                    $egg.eq(i).removeClass("init");
                    win();
                    $hammer.removeClass("hit");//
                    $hammer.addClass("shak");
    
                    //
                    arr.push(i);//
                   // $.cookie("eggIndex", arr, {expires: 1});//cookie
                }, 600);
            }, 600);
            $egg.eq(i).data("mark", false);//
        }

        function win(){
            $("#mask").show();
            $("#mask .winning").addClass('reback');
            setTimeout(()=> {
                $("#mask #card").addClass('pull');
            }, 500)
        }
}


